// React imports
import { useState } from 'react';

// App imports
import { LayerSelector } from './layers';
import './styles.scss';

export const Selectors = () => {
	const [ buildingVisibility, setBuildingVisibility ] = useState(false); 
	const [ landscapePointsVisibility, setLandscapePointsVisibility ] = useState(false); 
	const [ boundaryVisibility, setBoundaryVisibility ] = useState(false); 
	const [ streetsVisibility, setStreetsVisibility ] = useState(false); 

	return (
		<div className="selectors-wrapper">
			<LayerSelector
				activeLayer={boundaryVisibility}
				setActiveLayer={setBoundaryVisibility}
				name={"Boundary"}
			/>
			<LayerSelector
				activeLayer={streetsVisibility}
				setActiveLayer={setStreetsVisibility}
				name={"Streets"}
			/>
			<LayerSelector
				activeLayer={landscapePointsVisibility}
				setActiveLayer={setLandscapePointsVisibility}
				name={"Lanscape"}
			/>
			<LayerSelector
				activeLayer={buildingVisibility}
				setActiveLayer={setBuildingVisibility}
				name={"Buildings"}
			/>
		</div>
	)
}

Selectors.displayName="Selectors";