// App imports
import './styles.scss';

export const Header = () => {
	return (
		<div className="header-wrapper">
			<div style={{display: "flex", gap: "10px", padding: "10px"}}>
				<img 
					className="logo-wrapper"
					src={process.env.PUBLIC_URL + '/static/logos/icon.svg'}
					alt="icon-company" 
				/>
				<img 
					src={process.env.PUBLIC_URL + '/static/logos/logo.svg'}
					alt="logo-company" 
					width="150px"
				/>
			</div>
			<div></div>
			<div className="header-user">
				<img 
					className="user-icon"
					src={process.env.PUBLIC_URL + '/static/header/user_icon.png'}
					alt="user-icon" 
				/>
			</div>
		</div>
	)
}

Header.displayName="Header";